import React, { Component } from 'react'

export default class Prices extends Component {
    state = {
        currency: 'USD'
    }
    render() {
        return (
            <div>
                <ul className="list-group">
                    <li className="list-group-item"> Bitcoin rate for  :</li>
                    <li className="list-group-item">{this.props.bpi.USD.description}<span className=" badge badge-primary pl-4">{this.props.bpi.USD.code}</span></li>
                    <li className="list-group-item"><strong>{this.props.bpi.USD.rate}</strong> </li>
                </ul>
            </div>
        )
    }
}
